<!-- Lynn -->
<script>
import Upload from "@/components/Upload/Upload.vue";
function getTime() {
  let full = new Date();
  let date = full.toLocaleDateString();
  let time = full.toLocaleTimeString();

  // 將上、下午轉換成 24 小時制
  if (time[0] == "下") {
    let hour = time.substr(2).split(":");
    time = ((hour[0] * 1 + 12) % 12) + 12 + ":" + hour.slice(1).join(":");
  } else {
    time = time.substr(2);
  }
  return date + " " + time;
}

export default {
  name: "apply-component",
  components: { Upload },
  data() {
    return {
      prjNo: "", // 申請單號
      dayTime: getTime(), // 申請日期
      managerLoading: false,
      signerLoading: false,
      dialogVisible: true,
      hintVisible: false,

      form: {
        // 基本資訊
        project_No: null,
        project_Name: "", // 緊急搶修單主旨
        ld1: "", // 廠區名稱
        location_Type: "", // 無塵室/非無塵室
        work_Id: "", // FAB廠別/區域
        urgent_Type: "", // 風險種類(L/H)
        urgent_Reason: [], // 申請緊急搶修原因 [array]
        urgent_Reason_raw: [],
        project_No2: "",
        project_No4: "",

        dupety_Mamager_Empno: "", // 直屬主管/代理人
        dupety_Work_Signer: "", // 工作場所負責人
        begin_Date: "", // 施工時間(起)
        end_Date: "", // 施工時間(迄)
        s_Name: "", // 廠商名稱
        s_Id: "", // 廠商統編
        area_Name: "", // 廠商施工負責人
        duty_Tel: "", // 廠商緊急聯絡電話
        duty_Name: "", // 廠商安衛人員姓名
        work_Person_List: [], // 施工人員 [array]

        description: "", // 工作內容
        work_Type_Normal: "", // 一般/高風險作業(0/1)
        hightRiskType: [], // 作業選項 [array]
        hightRiskType_raw: [],
        card_No32: "",
        card_No35: "",
        card_No41: "",
        card_No42: "",
        card_No43: "",
        card_No44: "",
        card_No51: "",
        card_No71: "",
        card_No91: "",
        card_Other: "",

        isbuild: "0",
        isDraft: false, // 儲存草稿
      },
      rules: {
        project_No2: [{ required: false }], // 低風險工程單號
        project_No4: [{ required: false }], // 高風險工程單號
        begin_Date: [{ required: false }],
        end_Date: [{ required: false }],

        card_No32: [{ required: false }], // 施工架組配作業主管
        card_No35: [{ required: false }], // 屋頂作業主管
        card_No41: [{ required: false }], // 吊車
        card_No42: [{ required: false }], // 吊掛
        card_No43: [{ required: false }], // 指揮手
        card_No44: [{ required: false }], // 操作手
        card_No51: [{ required: false }], // 缺氣作業主管
        card_No71: [{ required: false }], // by pass 機台
        card_No91: [{ required: false }], // 有機/特化作業主管
        card_Other: [{ required: false }], // 其他項目
      },
    };
  },
  computed: {
    facList() {
      return this.$store.state.factory_list;
    },
    workAreaList() {
      let data = this.$store.state.workarea_list;
      if (!this.form.ld1 || !this.form.location_Type) {
        return data;
      } else {
        // 篩選 FAB 廠別/區域列表
        return data.filter(
          (x) =>
            x.location_Id == this.form.ld1 &&
            x.location_Type == this.form.location_Type
        );
      }
    },
    reasonList() {
      let data = this.$store.state.reason_list;
      if (this.form.urgent_Type) {
        // 篩選緊急搶修原因列表
        return data.filter((x) => x.urgent_Type == this.form.urgent_Type);
      } else {
        return [];
      }
    },

    managerList() {
      return this.$store.state.manager_list;
    },
    signerList() {
      return this.$store.state.signer_list;
    },

    // 緊急搶修時，可不上傳保險資料
    mustupload() {
      if (this.form.urgent_Reason_raw.indexOf("202004160005") != -1) {
        return [];
      } else {
        return [{ required: true, message: "必填", trigger: "change" }];
      }
    },

    workTypeList() {
      let data = this.$store.state.worktype_list;

      // 將工種依 class_Name (作業分類) 彙整
      let list = data.reduce((acc, cur) => {
        let index = acc.findIndex((item) => item.class_Name == cur.class_Name);
        if (index !== -1) {
          acc[index].value.push({
            class_Name: cur.class_Name,
            work_Type_Pk: cur.work_Type_Pk,
            work_Type_Name: cur.work_Type_Name,
            description: cur.description,
            approve_Item: cur.approve_Item,
            approve_She_Area: cur.approve_She_Area,
            extra_Common: cur.extra_Common,
          });
        } else {
          // 若找不到則新增此分類，否則向後 push 資料
          acc.push({
            class_Name: cur.class_Name,
            value: [
              {
                class_Name: cur.class_Name,
                work_Type_Pk: cur.work_Type_Pk,
                work_Type_Name: cur.work_Type_Name,
                description: cur.description,
                approve_Item: cur.approve_Item,
                approve_She_Area: cur.approve_She_Area,
                extra_Common: cur.extra_Common,
              },
            ],
          });
        }
        return acc;
      }, []);
      return list;
    },
  },
  methods: {
    dialogClose(done) {
      this.$confirm("確定要關閉？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },

    // 凡切換就清空所有輸入框，並取消欄位檢查
    urgentTypeChgHandler() {
      this.form.urgent_Reason_raw = [];
      this.form.project_No2 = "";
      this.rules.project_No2 = [];
      this.$refs.form.clearValidate("project_No2");

      this.form.project_No4 = "";
      this.rules.project_No4 = [];
      this.$refs.form.clearValidate("project_No4");
    },
    urgentReasonChgHandler(target, e) {
      // 低風險：選擇 on call 後增加檢查規則
      if (target.urgent_Type_Pk == "202004160002") {
        if (e) {
          this.rules.project_No2 = [
            { required: true, message: "工程單號不得為空", trigger: "blur" },
            {
              len: 12,
              message:
                "你選擇了[on call]項目，工程單號不得為空且應為12碼，請確認",
              trigger: "blur",
            },
          ];
        } else {
          this.form.project_No2 = "";
          this.rules.project_No2 = [];
          this.$refs.form.clearValidate("project_No2");
        }
      }
      // 高風險：有選無識別證、沒選未開立工程時增加規則
      if (target.urgent_Type_Pk == "202004160003") {
        if (e) {
          this.form.project_No4 = "";
          this.rules.project_No4 = [];
          this.$refs.form.clearValidate("project_No4");
        } else {
          if (this.form.urgent_Reason_raw.indexOf("202004160004") != -1) {
            this.rules.project_No4 = [
              {
                required: true,
                message:
                  "你選擇了[人員無識別證]項目，如無工程單編號，請一併勾選[未開立工程]",
                trigger: "blur",
              },
              {
                len: 12,
                message: "工程單號應為12碼，請確認",
                trigger: "blur",
              },
            ];
          }
        }
      }
      if (target.urgent_Type_Pk == "202004160004") {
        if (e) {
          if (this.form.urgent_Reason_raw.indexOf("202004160003") == -1) {
            this.rules.project_No4 = [
              {
                required: true,
                message:
                  "你選擇了[人員無識別證]項目，如無工程單編號，請一併勾選[未開立工程]",
                trigger: "blur",
              },
              {
                len: 12,
                message: "工程單號應為12碼，請確認",
                trigger: "blur",
              },
            ];
          }
        } else {
          this.form.project_No4 = "";
          this.rules.project_No4 = [];
          this.$refs.form.clearValidate("project_No4");
        }
      }
    },

    managerSearchHandler(evt) {
      this.managerLoading = true;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.throttledManagerSearch(evt);
      }
    },
    //2024.03.13 新增節流機制 managerSearchHandler() enter條件下的內容
    searchManager(evt) {
      this.managerLoading = false;
      if (!evt.target.value) return;
      this.$store.dispatch("getManagerList", evt.target.value);
    },
    signerSearchHandler(evt) {
      this.signerLoading = true;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.throttledSignerSearch(evt);
      }
    },
    //2024.03.13 新增節流機制 signerSearchHandler() enter條件下的內容
    searchSigner(evt) {
      this.signerLoading = false;
      if (!evt.target.value) return;
      this.$store.dispatch("getSignerList", evt.target.value);
    },
    dateRangeHandler() {
      if (this.form.begin_Date) {
        this.rules.begin_Date = [
          { validator: this.validateDate, trigger: "change" },
        ];
      } else {
        this.rules.begin_Date = [];
        this.$refs.form.clearValidate("begin_Date");
      }
      if (this.form.end_Date) {
        this.rules.end_Date = [
          { validator: this.validateDate, trigger: "change" },
        ];
      } else {
        this.rules.end_Date = [];
        this.$refs.form.clearValidate("end_Date");
      }
    },
    validateDate(rule, value, callback) {
      let full = new Date();
      let today =
        new Date(
          full.getFullYear(),
          full.getMonth(),
          full.getDate()
        ).getTime() / 1000;

      // 今天 <= 申請日期 <= 後天,修正為申請日期不得超過三天,不含今天
      let workdate = new Date(value).getTime() / 1000;
      if (workdate < today || workdate - today >= 4 * 24 * 60 * 60) {
        callback(new Error("申請日期不得超過三天"));
      } else {
        // 0 < 總施工時數(HR) <= 24
        if (this.form.begin_Date && this.form.end_Date) {
          let beginwork = new Date(this.form.begin_Date).getTime() / 1000;
          let endwork = new Date(this.form.end_Date).getTime() / 1000;
          if (beginwork >= endwork || endwork - beginwork > 24 * 60 * 60) {
            callback(new Error("時間範圍無效，或施工時數超過24小時"));
          } else {
            this.$refs.form.clearValidate("begin_Date");
            this.$refs.form.clearValidate("end_Date");
            callback();
          }
        } else {
          callback();
        }
      }
    },

    addConstruction() {
      const constructions = {
        name: "",
        id: "",
        birthday: "",
        card_Status: "",
        fileBase64: "",
        fileName: "",
        file_Id: "",
        key_Id: "",
      };
      this.form.work_Person_List.push(constructions);
    },
    deleteConstruction(index) {
      this.form.work_Person_List.splice(index, 1);
    },

    async validateCard(index, value) {
      // 凡重新驗證卡號，就將附檔資料清掉
      this.insuranceFileRmvHandler("", index);

      try {
        await this.$store.dispatch("getValidCard", {
          ld1: this.form.ld1,
          s_Id: this.form.s_Id,
          id: value,
        });
        this.form.work_Person_List[index].card_Status = this.$store.state
          .cardValid
          ? "1"
          : "0";
      } catch (err) {
        console.log(err);
      }
    },
    validateAge(rule, value, callback) {
      let full = new Date();
      let today =
        new Date(
          full.getFullYear(),
          full.getMonth(),
          full.getDate()
        ).getTime() / 1000;

      let birthdate = new Date(value).getTime() / 1000;
      if (today - birthdate < 18 * 365 * 24 * 60 * 60) {
        callback(new Error("未滿18歲，無法送出施工單"));
      } else {
        callback();
      }
    },

    // 新增保險檔案
    async insuranceFileChgHandler(file, index) {
      let raw = file[0].raw;
      let base64File = await this.toBase64(raw);

      // 取得檔名及 base64 轉檔，並移除欄位驗證
      this.form.work_Person_List[index].fileName = file[0].name;
      this.form.work_Person_List[index].fileBase64 = base64File;
      this.$refs.form.clearValidate("work_Person_List." + index + ".fileName");
    },
    insuranceFileRmvHandler(file, index) {
      this.form.work_Person_List[index].fileName = "";
      this.form.work_Person_List[index].fileBase64 = "";
      this.$refs.form.validateField("work_Person_List." + index + ".fileName");
    },
    toBase64(file) {
      return new Promise((resolve, reject) => {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = (error) => reject(error);
      });
    },
    // 刪除保險檔案
    async deleteFile(index, fileName, fileId) {
      try {
        await this.$store.dispatch("delInsuranceFile", { fileName, fileId });
        this.form.work_Person_List[index].file_Id = "";
        this.form.work_Person_List[index].fileName = "";
        this.form.work_Person_List[index].fileBase64 = "";
      } catch (err) {
        console.log(err);
      }
    },

    // 凡切換就清空所有輸入框，並取消欄位檢查
    workTypeChgHandler() {
      this.form.hightRiskType_raw = [];
      this.form.card_No32 = "";
      this.rules.card_No32 = [];
      this.$refs.form.clearValidate("card_No32");
      this.form.card_No35 = "";
      this.rules.card_No35 = [];
      this.$refs.form.clearValidate("card_No35");

      this.form.card_No41 = "";
      this.rules.card_No41 = [];
      this.$refs.form.clearValidate("card_No41");
      this.form.card_No42 = "";
      this.rules.card_No42 = [];
      this.$refs.form.clearValidate("card_No42");
      this.form.card_No43 = "";
      this.rules.card_No43 = [];
      this.$refs.form.clearValidate("card_No43");
      this.form.card_No44 = "";
      this.rules.card_No44 = [];
      this.$refs.form.clearValidate("card_No44");

      this.form.card_No51 = "";
      this.rules.card_No51 = [];
      this.$refs.form.clearValidate("card_No51");
      this.form.card_No71 = "";
      this.rules.card_No71 = [];
      this.$refs.form.clearValidate("card_No71");
      this.form.card_No91 = "";
      this.rules.card_No91 = [];
      this.$refs.form.clearValidate("card_No91");

      this.form.card_Other = "";
      this.rules.card_Other = [];
      this.$refs.form.clearValidate("card_Other");
    },
    cardInputHandler(target, e) {
      // 動火作業 (顯示提醒視窗)
      if (
        target.work_Type_Pk == "200310010002" ||
        target.work_Type_Pk == "201005250001"
      ) {
        if (e) {
          this.hintVisible = true;
        }
      }
      // 五米以上構築作業
      if (target.work_Type_Pk == "201103160001") {
        if (e) {
          this.rules.card_No32 = [
            { required: true, message: "必填", trigger: "blur" },
            { validator: this.validateLicense, trigger: "blur" },
          ];
        } else {
          this.form.card_No32 = "";
          this.rules.card_No32 = [];
          this.$refs.form.clearValidate("card_No32");
        }
      }
      // 屋頂作業
      if (target.work_Type_Pk == "202104200001") {
        if (e) {
          this.rules.card_No35 = [
            { required: true, message: "必填", trigger: "blur" },
            { validator: this.validateLicense, trigger: "blur" },
          ];
        } else {
          this.form.card_No35 = "";
          this.rules.card_No35 = [];
          this.$refs.form.clearValidate("card_No35");
        }
      }
      // 吊掛作業
      if (target.work_Type_Pk == "200310150005") {
        if (e) {
          this.rules.card_No41 = [
            { required: false, message: "選填", trigger: "blur" },
            //{ validator: this.validateLicense, trigger: "blur" },
          ];
          this.rules.card_No42 = [
            { required: true, message: "必填", trigger: "blur" },
            { validator: this.validateLicense, trigger: "blur" },
          ];
          this.rules.card_No43 = [
            { required: true, message: "必填", trigger: "blur" },
            { validator: this.validateLicense, trigger: "blur" },
          ];
          this.rules.card_No44 = [
            { required: true, message: "必填", trigger: "blur" },
            { validator: this.validateLicense, trigger: "blur" },
          ];
        } else {
          this.form.card_No41 = "";
          this.rules.card_No41 = [];
          this.$refs.form.clearValidate("card_No41");
          this.form.card_No42 = "";
          this.rules.card_No42 = [];
          this.$refs.form.clearValidate("card_No42");
          this.form.card_No43 = "";
          this.rules.card_No43 = [];
          this.$refs.form.clearValidate("card_No43");
          this.form.card_No44 = "";
          this.rules.card_No44 = [];
          this.$refs.form.clearValidate("card_No44");
        }
      }
      // 侷限空間作業
      if (target.work_Type_Pk == "200310150001") {
        if (e) {
          this.rules.card_No51 = [
            { required: true, message: "必填", trigger: "blur" },
            { validator: this.validateLicense, trigger: "blur" },
          ];
        } else {
          this.form.card_No51 = "";
          this.rules.card_No51 = [];
          this.$refs.form.clearValidate("card_No51");
        }
      }
      // Inter-Lock by pass
      if (target.work_Type_Pk == "200801090001") {
        if (e) {
          this.rules.card_No71 = [
            { required: false, message: "選填", trigger: "blur" },
            //{ required: true, message: "必填", trigger: "blur" },
          ];
        } else {
          this.form.card_No71 = "";
          this.rules.card_No71 = [];
          this.$refs.form.clearValidate("card_No71");
        }
      }
      // 危險管路拆卸鑽孔作業
      if (target.work_Type_Pk == "200310150006") {
        if (e) {
          this.rules.card_No91 = [
            { required: true, message: "必填", trigger: "blur" },
            { validator: this.validateLicense, trigger: "blur" },
          ];
        } else {
          this.form.card_No91 = "";
          this.rules.card_No91 = [];
          this.$refs.form.clearValidate("card_No91");
        }
      }
      // 其他
      if (target.work_Type_Pk == "200403220001") {
        if (e) {
          this.rules.card_Other = [
            { required: true, message: "必填", trigger: "blur" },
          ];
        } else {
          this.form.card_Other = "";
          this.rules.card_Other = [];
          this.$refs.form.clearValidate("card_Other");
        }
      }
    },
    hintClose(done) {
      this.$confirm("確定要關閉？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    async validateLicense(rule, value, callback) {
      const obj = {
        // 從所有施工人員，查對證號是否有效
        d: this.form.work_Person_List.map((x) => x.id),
        d2: this.form.s_Id,
        lincense: [value],
      };
      try {
        await this.$store.dispatch("getValidLicense", obj);
        if (!this.$store.state.idValid) {
          callback(new Error("證照號碼不正確"));
        } else {
          callback();
        }
      } catch (err) {
        console.log(err);
      }
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.form.work_Person_List = [];
      this.addConstruction();
    },
    submitForm(formName, saveDraft) {
      this.form.isDraft = saveDraft;

      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 將證號填入相對應的欄位 (緊急搶修原因)
          this.form.urgent_Reason = [];
          for (let i = 0; i < this.form.urgent_Reason_raw.length; i++) {
            let type_key = this.form.urgent_Reason_raw[i];
            let item = this.reasonList.filter(
              (e) => e.urgent_Type_Pk == type_key
            )[0];

            if (type_key == "202004160002") {
              item.extra_Common = this.form.project_No2;
            } else if (type_key == "202004160004") {
              item.extra_Common = this.form.project_No4;
            }
            this.form.urgent_Reason.push(item);
          }

          // 將證號填入相對應的欄位 (高風險工種)
          this.form.hightRiskType = [];
          for (let i = 0; i < this.form.hightRiskType_raw.length; i++) {
            let type_key = this.form.hightRiskType_raw[i];
            let item = this.$store.state.worktype_list.filter(
              (e) => e.work_Type_Pk == type_key
            )[0];

            if (type_key == "201103160001") {
              item.extra_Common = this.form.card_No32;
            } else if (type_key == "202104200001") {
              item.extra_Common = this.form.card_No35;
            } else if (type_key == "200310150005") {
              item.extra_Common = `${this.form.card_No41}, ${this.form.card_No42}, ${this.form.card_No43}, ${this.form.card_No44}`;
            } else if (type_key == "200310150001") {
              item.extra_Common = this.form.card_No51;
            } else if (type_key == "200801090001") {
              item.extra_Common = this.form.card_No71;
            } else if (type_key == "200310150006") {
              item.extra_Common = this.form.card_No91;
            } else if (type_key == "200403220001") {
              item.extra_Common = this.form.card_Other;
            }
            this.form.hightRiskType.push(item);
          }

          try {
            await this.$store.dispatch("submitApply", this.form);
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "申請成功",
            });
            // 更新頁面
            this.$router.go(0);
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    //2024.03.13 新增節流機制
    throttledManagerSearch: null,
    throttledSignerSearch: null,
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // footer 固定、新增第一列施工人員
    const positionFixed = document.querySelector(".position-fixed");
    const positionFixedHeight = positionFixed.offsetHeight;
    const apply = document.querySelector(".apply");
    apply.style.marginBottom = `${positionFixedHeight}px`;

    try {
      // 判斷有無單號 (有=>編輯草稿、無=>新申請)
      this.prjNo = this.$route.params.prjNo;
      if (this.$route.params.prjNo) {
        let getApplyDataBRes = await this.$store.dispatch(
          "getApplyDataB",
          this.$route.params.prjNo
        );

        let ocopy = { ...getApplyDataBRes.data.data.insertData };
        // [資料加工] 緊急搶修原因 (複選、輸入框)
        ocopy.urgent_Reason_raw = [];
        ocopy.project_No2 = "";
        ocopy.project_No4 = "";

        for (let i = 0; i < ocopy.urgent_Reason.length; i++) {
          let type_Pk = ocopy.urgent_Reason[i].urgent_Type_Pk;
          ocopy.urgent_Reason_raw.push(type_Pk);
          if (type_Pk == "202004160002") {
            ocopy.project_No2 = ocopy.urgent_Reason[i].extra_Common;
            this.rules.project_No2 = [
              { required: true, message: "工程單號不得為空", trigger: "blur" },
              {
                len: 12,
                message:
                  "你選擇了[on call]項目，工程單號不得為空且應為12碼，請確認",
                trigger: "blur",
              },
            ];
          } else if (type_Pk == "202004160004") {
            ocopy.project_No4 = ocopy.urgent_Reason[i].extra_Common;
            if (ocopy.urgent_Reason_raw.indexOf("202004160003") == -1) {
              this.rules.project_No4 = [
                {
                  required: true,
                  message:
                    "你選擇了[人員無識別證]項目，如無工程單編號，請一併勾選[未開立工程]",
                  trigger: "blur",
                },
                {
                  len: 12,
                  message: "工程單號應為12碼，請確認",
                  trigger: "blur",
                },
              ];
            }
          }
        }

        // [資料加工] 高風險工種 (複選、輸入框)
        ocopy.hightRiskType_raw = [];
        ocopy.card_No32 = "";
        ocopy.card_No35 = "";
        ocopy.card_No41 = "";
        ocopy.card_No42 = "";
        ocopy.card_No43 = "";
        ocopy.card_No44 = "";
        ocopy.card_No51 = "";
        ocopy.card_No71 = "";
        ocopy.card_No91 = "";
        ocopy.card_Other = "";

        for (let i = 0; i < ocopy.hightRiskType.length; i++) {
          let type_Pk = ocopy.hightRiskType[i].work_Type_Pk;
          ocopy.hightRiskType_raw.push(type_Pk);
          if (type_Pk == "201103160001") {
            ocopy.card_No32 = ocopy.hightRiskType[i].extra_Common;
            this.rules.card_No32 = [
              { required: true, message: "必填", trigger: "blur" },
              { validator: this.validateLicense, trigger: "blur" },
            ];
          } else if (type_Pk == "202104200001") {
            ocopy.card_No35 = ocopy.hightRiskType[i].extra_Common;
            this.rules.card_No35 = [
              { required: true, message: "必填", trigger: "blur" },
              { validator: this.validateLicense, trigger: "blur" },
            ];
          } else if (type_Pk == "200310150005") {
            let item = ocopy.hightRiskType[i].extra_Common.split(",");
            ocopy.card_No41 = item[0];
            ocopy.card_No42 = item[1];
            ocopy.card_No43 = item[2];
            ocopy.card_No44 = item[3];
            this.rules.card_No41 = [
              { required: true, message: "必填", trigger: "blur" },
              { validator: this.validateLicense, trigger: "blur" },
            ];
            this.rules.card_No42 = [
              { required: true, message: "必填", trigger: "blur" },
              { validator: this.validateLicense, trigger: "blur" },
            ];
            this.rules.card_No43 = [
              { required: true, message: "必填", trigger: "blur" },
              { validator: this.validateLicense, trigger: "blur" },
            ];
            this.rules.card_No44 = [
              { required: true, message: "必填", trigger: "blur" },
              { validator: this.validateLicense, trigger: "blur" },
            ];
          } else if (type_Pk == "200310150001") {
            ocopy.card_No51 = ocopy.hightRiskType[i].extra_Common;
            this.rules.card_No51 = [
              { required: true, message: "必填", trigger: "blur" },
              { validator: this.validateLicense, trigger: "blur" },
            ];
          } else if (type_Pk == "200801090001") {
            ocopy.card_No71 = ocopy.hightRiskType[i].extra_Common;
            this.rules.card_No71 = [
              { required: true, message: "必填", trigger: "blur" },
            ];
          } else if (type_Pk == "200310150006") {
            ocopy.card_No91 = ocopy.hightRiskType[i].extra_Common;
            this.rules.card_No91 = [
              { required: true, message: "必填", trigger: "blur" },
              { validator: this.validateLicense, trigger: "blur" },
            ];
          } else if (type_Pk == "200403220001") {
            ocopy.card_Other = ocopy.hightRiskType[i].extra_Common;
            this.rules.card_Other = [
              { required: true, message: "必填", trigger: "blur" },
            ];
          }
        }
        // 將重製好之資料覆蓋表單
        this.form = ocopy;
      } else {
        this.addConstruction();
        await this.$store.dispatch("getApplyDataA");
      }
    } catch (err) {
      console.log(err);
    }
  },
  //2024.03.13 新增節流機制
  //頁面初始前，先執行的階段
  created() {
    // 使用throttle包裝方法
    this.throttledManagerSearch = this.throttle(
      this.searchManager,
      2500,
      false
    );
    this.throttledSignerSearch = this.throttle(this.searchSigner, 2500, false);
  },
};
</script>

<template src="./apply.html"></template>
